.hits_title {
  font-size: 40px;
  color: rgb(207, 114, 114);
  padding-top: 20px;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  width: 100%;
  height: 100px;
}
.hits_list {
  list-style: none;
  display: block;
  width: 70%;
  margin: 0 auto;
  background-color: rgb(235, 159, 197);
  padding: 10px;
  border-radius: 3%;
}
